.faq, .rations__slider, .rations__header, .sets__slider, .sets__header, .about-product {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 24px;
}

.sets-item__content, .sets-item, .intro__inner, .intro {
  display: flex;
  flex-direction: column;
}

.site-nav {
  display: flex;
  align-items: center;
}

.contacts__title, .norms__item-title, .section__title, .intro__subtitle {
  font-family: "Oswald", sans-serif;
  line-height: 1.3;
  letter-spacing: 0.02em;
  font-weight: 700;
}

body {
  margin: 0;
  background-color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;
  color: #434242;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.text-uppercase {
  text-transform: uppercase;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #000;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #000;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1248px;
  padding: 0 24px;
  margin: 0 auto;
}

/* Components */
.page {
  overflow: hidden;
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 112px;
}
@media (max-width: 991px) {
  .header__nav {
    height: 100px;
  }
}
@media (max-width: 375px) {
  .header__nav {
    height: 70px;
  }
}

.logo {
  margin-right: 20px;
  flex-shrink: 0;
  width: 80px;
  height: 80px;
  transition: opacity 0.1s linear;
}
@media (max-width: 991px) {
  .logo {
    width: 65px;
    height: 65px;
  }
}
@media (max-width: 375px) {
  .logo {
    width: 60px;
    height: 60px;
    align-self: flex-end;
  }
}
.logo:hover {
  opacity: 0.9;
}
.logo:active {
  opacity: 0.7;
}

@media (max-width: 991px) {
  .site-nav {
    display: none;
  }
}
.site-nav__item {
  margin-right: 33px;
}
.site-nav__item:last-child {
  margin-right: 12px;
}
.site-nav__link {
  padding: 3px 10px;
  font-weight: 500;
  color: #fff;
  position: relative;
  transition: opacity 0.1s linear;
}
.site-nav__link:after {
  content: "";
  display: block;
  width: calc(100% - 20px);
  height: 2px;
  position: absolute;
  top: 100%;
  left: 50%;
  background-color: #961914;
  opacity: 0;
  transform: translateX(-50%);
  transition: opacity 0.2s linear;
}
.site-nav__link:hover:after {
  opacity: 1;
}
.site-nav__link:active {
  opacity: 0.7;
}

.burger {
  display: none;
  width: 32px;
  height: 24px;
  padding: 3px 0 3px 3px;
