.btn {
  display: inline-block;
  vertical-align: middle;
  padding: 13px 42px;
  font-size: inherit;
  font-weight: 600;
  line-height: 1.4;
  color: #fff;
  text-align: center;
  border: 0;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s linear;
}
.btn--primary {
  background-color: #961914;
}
.btn--primary:hover {
  background-color: #75110D;
}
.btn--secondary {
  color: #961914;
  border: 1px solid #961914;
  background-color: transparent;
}
.btn--secondary:hover {
  border-color: #EAD1D0;
  background-color: #EAD1D0;
}
.btn--transparent {
  width: 223px;
  padding: 10px 16px 8px 43px;
  font-size: 14px;
  font-weight: 500;
  background-color: rgba(255, 255, 255, 0.2);
}
.btn--transparent:hover {
  background-color: rgba(255, 255, 255, 0.4);
}
.btn--light {
  padding-left: 60px;
  padding-right: 60px;
  font-weight: 700;
  color: #EC7973;
  background-color: #FFF4F3;
}
.btn--light:hover {
  color: #961914;
  background-color: #EAD1D0;
}

